import React from "react";
import { Col, Row } from "react-bootstrap";
import { Breadcrumb, CommonButton, PageHeader, Input as TextInput, TextEditor, AntTextArea, ListingHeader  } from "../../../../components";

function TermsAndConditions() {
  const breadcrumb = [
    {
      path: "/siteadmin/dashboard",
      name: "DASHBOARD",
    },
    {
      path: "#",
      name: "TERMS & CONDITIONS",
    },
  ];
  return (
    <>
    <div className="components-preview">
      <div className="nk-block-head nk-block-head-sm">
        <div className="nk-block-between">
          <PageHeader heading="Terms & Conditions">
            <Breadcrumb breadcrumb={breadcrumb} />
          </PageHeader>
          <ListingHeader
            btnArray={["extraButton"]}
            extraBtnText="Preview"
            extraBtnClass="btn btn-primary"
          />
        </div>
      </div>
      <div className="nk-block">
        <div className="card">
          <div className="card-inner">
            <Row>
              <Col lg={12}>
                <form>
                  <div className="form-group">
                      <label className="form-label">Meta Title</label>
                      <div className="form-control-wrap">
                          <TextInput
                              id="metaTitle"
                              className="form-control form-control-lg"
                              name="metaTitle"
                              disabled={false}
                              variant="standard"
                              type="text"
                              placeholder="Enter Meta Title"
                              setFieldValue=""
                              icon=""
                          />
                      </div>
                  </div>
                  <div className="form-group">
                      <label className="form-label">Meta Description</label>
                      <div className="form-control-wrap">
                          <AntTextArea
                              className="form-control form-control-lg no-resize"
                              name="metaDescription"
                              disabled={false}
                              variant="standard"
                              placeholder="Enter Meta Description"
                              setFieldValue=""
                              icon=""
                              rows="2"
                          />
                      </div>
                  </div>
                  <div className="form-group">
                    <label className="form-label">Content</label>
                    <TextEditor />
                  </div>
                  <CommonButton extraClassName="btn btn-lg btn-primary my-3" htmlType="submit" type="submit">Update</CommonButton>
                </form>
              </Col>
            </Row>
          </div>
        </div>
      </div>
    </div>
    </>
  );
}

export default TermsAndConditions;
